.menu {
  background-color: $accent-2;
  &-background {
    width: 100%;
    max-height: 275px;
    object-fit: cover;
  }
  &-content {
    border-radius: 3rem;
    position: relative;
    top: -50px;
    background-color: $accent-2;
    &--title {
      display: flex;
      flex-direction: row;
      justify-content: start;
      align-items: center;
      padding: 1.5rem;
      h2 {
        padding-right: 2rem;
      }
    }
    &--dishList {
      padding: 1rem 1rem;
      &_title {
        text-transform: uppercase;
        padding-bottom: 3px;
        font-size: 16px;
        font-weight: 300;
        border-bottom: 3px solid $accent-border;
        width: 40px;
        margin-bottom: 0.5rem;
      }
    }
    &--button {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 1.5rem;
      button {
        width: 190px;
      }
    }
  }
}
